import { FETCH_TASK_COUNT, SET_ERROR, SET_PAGINATION, SET_SEARCH, SET_SUBORDINATES, SET_TASK, SET_USER } from './type';

const reducer = (state, action) => {
  const { payload } = action;
  switch (action.type) {
    case SET_USER:
      return { ...state, user: payload.user };
    case SET_TASK:
      return { ...state, tasks: payload.tasks, pagination: payload.pagination };
    case SET_SUBORDINATES:
      return { ...state, pagination: payload.subordinates };
    case SET_PAGINATION:
      return { ...state, pagination: payload.pagination };
    case SET_SEARCH:
      return { ...state, search: payload.search };
    case SET_ERROR:
      return { ...state, error: payload.error };
    case FETCH_TASK_COUNT: {
      return { ...state, count: payload.count };
    }
    default:
      return state;
  }
};

export default reducer;
