import React from 'react';

import ReactTooltip from 'react-tooltip';
import { useTranslation } from 'react-i18next';
import PropTypes from 'prop-types';
import WarningOutlinedIcon from '@material-ui/icons/WarningOutlined';

function randomLabelGenerator() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    const r = (Math.random() * 16) | 0;
    const v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

function PendingActionAlert(props) {
  const { t } = useTranslation();
  const label = randomLabelGenerator();

  return (
    <span>
      <WarningOutlinedIcon alt="" style={{ color: 'red' }} data-tip data-for={label} />
      <ReactTooltip id={label} type="error" effect="solid" place={props.positionPlacement}>
        <span>{t(props.popupTextKey)}</span>
      </ReactTooltip>
    </span>
  );
}

PendingActionAlert.propTypes = {
  positionPlacement: PropTypes.string.isRequired,
  popupTextKey: PropTypes.string
};

PendingActionAlert.defaultProps = {
  positionPlacement: 'bottom',
  popupTextKey: 'message.pendingActions'
};

export default PendingActionAlert;
