import { OUTPUT_EVENT_TYPES } from 'custom-elements/widgetEventTypes';
import { publishWidgetEvent } from 'helpers/widgetEvents';
import PropTypes from 'prop-types';
import React from 'react';

const RoleHoverEventDispatcher = props => {
  const { children } = props;
  const { role } = children.props;
  const initial = role.substr(0, 1).toUpperCase();

  return (
    <div
      className="event-dispatcher"
      onMouseOver={event => publishWidgetEvent(OUTPUT_EVENT_TYPES.onMouseEnterRole, { x: event.clientX, y: event.clientY, role: initial })}
      onFocus={() => {}}
      onMouseOut={() => publishWidgetEvent(OUTPUT_EVENT_TYPES.onMouseLeaveRole)}
      onBlur={() => {}}
    >
      {children}
    </div>
  );
};

RoleHoverEventDispatcher.propTypes = {
  children: PropTypes.shape({ props: PropTypes.shape({ role: PropTypes.string.isRequired }).isRequired }).isRequired
};

export default RoleHoverEventDispatcher;
